<div class="hello"><router-link :to="'/flag/'+h.uid"><div class="image"><h3 class="ui header"><img class="main" :src="getIcon(h)" alt="^_^">{{h.name}}<span v-if="h.learner_birth">-<span v-html="toAge(h.learner_birth)"></span>歲</span><span v-if="h.child_birth">(孩子約</span><span v-if="h.child_birth2">{{toAge(h.child_birth2)}} ~</span><span v-if="h.child_birth">{{toAge(h.child_birth)}} 歲)</span></h3></div><div class="content"><p class="description gray"> <span v-html="highlight(h.address, mySearch)"></span>&nbsp;&nbsp; - {{countDateDiff(h.lastUpdate)}}已更新</p><p class="description" v-if="h.learner_habit &amp;&amp; full" v-html="'興趣： ' + highlight(h.learner_habit, mySearch)"></p><p class="description" v-if="h.share" v-html="'可分享： ' + highlight(h.share, mySearch)"></p><p class="description" v-if="h.ask" v-html="'尋找： ' + highlight(h.ask, mySearch)"></p><p class="description" v-if="h.price" v-html="'可支付： ' + highlight(h.price, mySearch)"></p></div><div class="content" v-if="h.connect_me &amp;&amp; full"><div class="ui divider"></div><p class="descrtpion">連絡方式： {{h.connect_me}}</p><p class="descrtpion" v-if="h.freetime">有空時間： {{h.freetime}}</p></div><div class="content"><div class="ui divider"></div><p v-if="h.site"><a @click.stop="goto(h.site)">個人網址：<img :src="'https://www.google.com/s2/favicons?domain=' + h.site" title="個人網址" alt="個人網址"><i class="right arrow icon"></i></a></p><p v-if="h.site2"><a @click.stop="goto(h.site2)">社群網址：<img :src="'https://www.google.com/s2/favicons?domain=' + h.site2" title="社群網址" alt="個人網址"><i class="right arrow icon"></i></a></p></div><div class="content"><div class="ui divider"></div><p class="descrtpion" v-if="!full"><span class="text" v-html="highlight(part(h.note), mySearch) + '...'"></span></p><p class="descrtpion" v-else><span class="text" v-html="highlight(h.note, mySearch)"></span></p></div></router-link><div class="filler"></div><div class="ui bottom attached buttons"><div class="ui green basic button" @click="addBook(h.uid || h.idx)" v-if="uid &amp;&amp; (!book || book.indexOf(h.uid || h.idx) == -1)"><i class="book icon"></i>加入名簿</div><div class="ui red basic button" @click="removeBook(book.indexOf(h.uid || h.idx))" v-else-if="uid &amp;&amp; book &amp;&amp; book.indexOf(h.uid || h.idx) &gt; -1"><i class="book icon"></i>從名簿移除</div><router-link class="ui blue basic button" to="/profile" v-else><i class="book icon"></i>註冊帳號以建立名簿</router-link><div class="ui pink basic button" @click="locate(h, true)"><i class="map icon"></i>地圖檢視</div></div></div>